import React, { useState } from 'react';

import { InputMask } from 'primereact/inputmask';
import { Calendar } from 'primereact/calendar';

import 'primereact/resources/themes/lara-light-cyan/theme.css';
import 'primeicons/primeicons.css';
import { InputSwitch } from 'primereact/inputswitch';
import { addLocale } from 'primereact/api';
import { InputText } from 'primereact/inputtext';
import { Button } from 'primereact/button';
import { RadioButton } from 'primereact/radiobutton';

const Order = ({
  checked,
  setChecked,
  setIsOrderFinish,
  datetime24h,
  setDateTime24h,
  setVisible,
  items,
  countById,
  totalItems,
  orderType,
  setOrderType,
  totalPrice,
  sendOrder,
}) => {
  const [address, setAddress] = useState('');
  const [phoneNum, setPhoneNum] = useState('');
  const [commentValue, setCommentValue] = useState('');

  const [phoneMaskValue, setPhoneMaskValue] = useState();

  const [payValue, setPayValue] = useState('');

  function changeValue(e) {
    setPayValue(e.target.value);
  }

  const handleAddressChange = (event) => {
    setAddress(event.target.value);
  };

  const handlePhoneNumChange = (event) => {
    setPhoneNum(event.target.value);
  };

  const handleCommentChange = (event) => {
    setCommentValue(event.target.value);
  };

  const dishesList = items.map((i) => {
    const count = countById(totalItems, i.id, i.activeSize);
    const value = `${i.name} | ${i.serving ? i.serving + ' |' : ''} | ${i.price}  | x ${count}.`;
    return value;
  });

  addLocale('ru', {
    firstDayOfWeek: 1,
    dayNames: ['', '', '', '', '', '', ''],
    dayNamesShort: ['', '', '', '', '', '', ''],
    dayNamesMin: ['', '', '', '', '', '', ''],
    monthNames: [
      '',
      '',
      '',
      '',
      '',
      '',
      '',
      '',
      '',
      '',
      '',
      '',
    ],
    monthNamesShort: [
      '',
      '',
      '',
      '',
      '',
      '',
      '',
      '',
      '',
      '',
      '',
      '',
    ],
    today: '',
    clear: '',
  });

  const [loading, setLoading] = useState(false);

  const load = () => {
    setLoading(true);
    sendOrder(orderType, address, phoneNum, commentValue, dishesList.toString(), items, countById, totalItems, payValue);
    setTimeout(() => {
      setLoading(false);
      setVisible(false);
      setIsOrderFinish(true)
    }, 1000);
  };
  return (
    <div className="w-full h-full">
      <div className="h-full">
        <div className="w-full flex justify-center my-3">
          <div className="bg-light-gray rounded-md">
            <button
              className={`text-white w-28 py-1 rounded-md transition-all ${
                orderType === '' ? 'bg-lightSlate-gray' : 'bg-light-gray'
              }`}
              onClick={() => {
                setOrderType('');
              }}>
              
            </button>
            <button
              className={`text-white w-28 py-1 rounded-md transition-all ${
                orderType === '' ? 'bg-lightSlate-gray' : 'bg-light-gray'
              }`}
              onClick={() => {
                setOrderType('');
              }}>
              
            </button>
          </div>
        </div>
        <div className=" flex flex-col h-full overflow-y-scroll">
          <div className="max-h-32 overflow-y-auto flex flex-col mb-6 border-dashed border p-2 rounded-lg">
            {items.map((i) => {
              const count = countById(totalItems, i.id, i.activeSize);
              return (
                <span key={i.id} className="w-full text-sm">{`${i.name} | ${
                  i.serving ? i.serving + ' |' : ''
                } ${i.price}  | x ${count}.`}</span>
              );
            })}
          </div>
          <div className="flex flex-col">
            {
              totalPrice < 1000 && orderType === '' ? (
                <>
                  <span className="mb-2  text-[12px]">
                     : <b className="text-lightSlate-gray">{totalPrice}</b> 
                  </span>
                  <span className='mb-2 text-[12px]'> : <b className="text-lightSlate-gray">200</b> </span>
                  <span className='mb-6'>: <b className="text-lightSlate-gray">{totalPrice + 200}</b> </span>
                </>
              ) : (
                <span className="mb-6">
                  : <b className="text-lightSlate-gray">{totalPrice < 1000 && orderType === '' ? totalPrice + 200 : totalPrice}</b> 
                </span>
                )
            }
            
            <div className="flex-auto w-auto">
              <div className="w-full flex justify-between">
                <label htmlFor="calendar-24h" className="font-bold block mb-2">
                  {`  ${orderType === '' ? '' : ''}`}
                </label>
                <InputSwitch checked={checked} onChange={(e) => setChecked(e.value)} />
              </div>
              {checked && (
                <div className="w-auto h-auto bg-white">
                  <Calendar
                    id="calendar-24h"
                    value={datetime24h}
                    onChange={(e) => setDateTime24h(e.value)}
                    showIcon
                    showTime
                    locale="ru"
                    dateFormat="dd.mm.yy"
                    hourFormat="24"
                  />
                </div>
              )}
            </div>

            {orderType === '' && (
              <div className="flex flex-col">
                <label className="mb-1">  :</label>
                <InputText
                  value={address}
                  className="w-[60%]"
                  onChange={handleAddressChange}
                  placeholder=". , 13"
                />
              </div>
            )}
            <label className="mb-1">   :</label>
            <InputMask
              value={phoneMaskValue}
              type='tel'
              className="w-[60%]"
              onChange={(e) => {
                setPhoneMaskValue(e.target.value);
                handlePhoneNumChange(e);
              }}
              mask="+7(999)999-99-99"
              placeholder="+7(978)879-62-20"
            />

            <div className="w-full flex flex-col mb-6">
              <label className="mb-1"> :</label>
              <InputText
                value={commentValue}
                className="w-[60%]"
                onChange={handleCommentChange}
                placeholder=":  "
              />
            </div>
              {orderType === '' && (
                <div className="flex flex-col mb-6" name="checkbox">
                  <label className="mb-3"> :</label>
                  <div className="mb-2">
                    <RadioButton inputId="cash" name="cash" value="" onChange={changeValue} checked={payValue === '' ? true : false} />
                    <label htmlFor="cash" className="ml-2"></label>
                  </div>
                  <div className="payment_method">
                    <RadioButton inputId="cart" name="cart" value="" onChange={changeValue} checked={payValue === '' ? true : false} />
                    <label htmlFor="cart" className="ml-2"></label>
                  </div>
                </div>
              )}
            
            <Button
                label=""
                disabled={orderType === '' ? !phoneMaskValue || !address : !phoneMaskValue}
                icon="pi pi-check"
                loading={loading}
                onClick={load}
                iconPos="right"
                className=" w-auto h-auto px-4 py-2"
              />
          </div>
        </div>
      </div>
    </div>
  );
};

export default Order;
