import React from 'react';

const OrderFinish = ({ orderValues, shortDate, shortTime }) => {
  return (
    <>
      {orderValues.orderType === '' ? (
        <div className="w-full flex flex-col">
          <span className="mb-2">
            <b> : </b> {orderValues.orderType}
          </span>
          <span className="mb-2">
            <b>:</b> {orderValues.address}
          </span>
          <span className="mb-2">
            <b> :</b> {orderValues.phoneNumber}
          </span>
          <span className="mb-2">
            <b>:</b> {orderValues.comment ? orderValues.comment : ' '}
          </span>
          <span className="mb-2">
            <b>:</b>
          </span>
          {orderValues.items.map((i) => {
            const count = orderValues.countById(orderValues.totalItems, i.id, i.activeSize);
            return (
              <span key={i.id} className="w-full text-sm">{`${i.name} | ${i.price} . | x ${count} .`}</span>
            );
          })}
          <span className="my-2"><b>:</b> {orderValues.totalPrice} </span>
          <span className="mb-2">
            <b>:</b> {orderValues.checked ? shortDate : ''}
          </span>
          <span className="mb-2">
            <b>:</b> {orderValues.checked ? shortTime : ''}
          </span>
          <span className="mb-2">
            <b> :</b> {orderValues.pay ? orderValues.pay : ' '}
          </span>
        </div>
      ) : (
        <div className="w-full flex flex-col">
          <span className="mb-2">
            <b> : </b> {orderValues.orderType}
          </span>
          <span className="mb-2">
            <b> :</b> {orderValues.phoneNumber}
          </span>
          <span className="mb-2">
            <b>:</b> {orderValues.comment ? orderValues.comment : ' '}
          </span>
          <span className="mb-2">
            <b>:</b>
          </span>
          {orderValues.items.map((i) => {
            const count = orderValues.countById(orderValues.totalItems, i.id, i.activeSize);
            return (
              <span key={i.id} className="w-full text-sm">{`${i.name} | ${i.price} . | x ${count} .`}</span>
            );
          })}
          <span className="my-2"><b>:</b> {orderValues.totalPrice}  </span>
          <span className="mb-2">
            <b>:</b> {orderValues.checked ? shortDate : ''}
          </span>
          <span className="mb-2">
            <b>:</b> {orderValues.checked ? shortTime : ''}
          </span>
        </div>
      )}
    </>
  );
};

export default OrderFinish;
